/*——————边栏最热文章样式————————*/
.hot-list{
    padding: 0;
}

.hot-list a :hover{
    background-color: #FBFBFB;
}

.hot-list a :hover .title{
    color: #66ccff !important;
}

.hot-item{
    height: 120px;
    padding: 5px 0;
    border-bottom: solid 1px #eee;
}

.hot-item .col-md-4{
    padding: 0 5px;
}

.hot-item .col-md-8{
    padding: 0 5px 5px 5px;
}

.hot-item p{
    margin: 0 5px 5px 5px;
}

.hot-item .subhead{
    font-size: 12px;
    color: #bbb;
}

.hot-item .title{
    color: #888;
}


