a:hover {
    text-decoration: none;
}

.navbar-right a:hover{
    color: #66ccff !important;
}

img{
    width: 100%;
    height: auto;
}

li{
    list-style-type:none;
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.theme-color{
    /*主题颜色*/
    color: #66ccff !important;
}

.theme-bg-color{
    background-color: #66ccff !important;
}

.btn-theme{
    background-color: #66ccff !important;
    color:#FFFFFF;
}

.btn-theme:hover{
    color:#FFFFFF !important;
}

.min-height-800{
    min-height:800px;
}

.text-center{
    text-align:center
}

.article{
    padding: 25px;
}

@media screen and (max-width: 846px) {
    .article{
        padding: 15px;
    }
}

.logo{
    width:30px;
    height:30xp;
    float:left;
    margin-top:-5px;
    margin-right:10px;
}

.avatar {
    width: 30px;
    height: 30px;
    margin-right: 5px;
    margin-top: -5px;
    border: 1px solid white;
    border-radius: 50%;
}

.panel-no-radius{
    border-radius: 0px !important;
}
.navbar-no-radius{
    border-width: 0 0 1px;
    border-radius: 0px;
}

.float-right{
    float: right;
}

/* article list css */
.introduction{
    height:110px;
    /* white-space:nowrap; */
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
}

.article-list:hover{
    background-color: #FAFAFA;
}

.article-list p{
    color:#000000 !important;
}

.article-list .title:hover{
    color: #66ccff !important;
}


